export default {
	data() {
		return {
			pwd: '',
			newPwd: '',
			rePwd: '',
			sex: 0,
			submitStatus: false,
			oldPassword: true
		}
	},
	computed: {},
	methods: {
		// 保存资料
		submitHandler() {
			this.submitStatus = true;
			if (this.oldPassword == true) {
				if (this.pwd === '') {
					this.$common.errorToShow('请输入旧密码')
					this.submitStatus = false;
				} else if (this.newPwd === '') {
					this.$common.errorToShow('请输入新密码')
					this.submitStatus = false;
				} else if (this.rePwd === '') {
					this.$common.errorToShow('请输入重复密码')
					this.submitStatus = false;
				} else {
					this.$api.editPwd({
						password: this.pwd,
						newPassword: this.newPwd,
						rePassword: this.rePwd
					}, res => {
						if (res.status){
							// this.submitStatus = false;
							this.$common.successToShow(res.msg)
							setTimeout(function(){
								uni.navigateBack({
									delta: 1
								});
							},1000)
						} else {
							// this.submitStatus = false;
							this.$common.errorToShow(res.msg)
						}
					},res => {
						this.submitStatus = false;
					})
				}
			} else {
				if (this.newPwd === '') {
					this.$common.errorToShow('请输入新密码')
					this.submitStatus = false;
				} else if (this.rePwd === '') {
					this.$common.errorToShow('请输入重复密码')
					this.submitStatus = false;
				} else {
					this.$api.editPwd({
						newpwd: this.newPwd,
						repwd: this.rePwd,
					}, res => {
						if (res.status){
							// this.submitStatus = false;
							this.$common.successToShow(res.msg)
							setTimeout(function(){
								uni.navigateBack({
									delta: 1
								});
							},1000)
						} else {
							// this.submitStatus = false;
							this.$common.errorToShow(res.msg)
						}
					},res => {
						this.submitStatus = false;
					})
				}
			}
		}
	},
	onLoad: function() {
		var _this = this;
		_this.$api.userInfo({}, function(res) {
			if (res.status) {
				var the_sex = res.data.sex - 1;
				if (res.data.birthday == null) {
					res.data.birthday = '请选择';
				}
				_this.nickname = res.data.nickname;
				_this.mobile = res.data.mobile;
				_this.sex = the_sex;
				_this.index = the_sex;
				_this.birthday = res.data.birthday;
				_this.avatar = res.data.avatar;
				// _this.oldPassword = res.data.password;
				if (_this.birthday != '请选择') {
					_this.date = _this.birthday;
				}
			} else {
				//报错了
				_this.$common.errorToShow(res.msg);
			}
		});
	}
}
